/* font-family: 'Cherry Swash', cursive; heading
font-family: 'Poppins', sans-serif; readable */

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  background: linear-gradient(0.45turn, black, gray);
  color: #fff;
  height: 100vh;
  font-family: 'Poppins', sans-serif;
}


header {
  padding: 2rem;
}

header > h1 {
  font-family: 'Cherry Swash', cursive;
  font-size: 3rem;
}

header * {
  padding: 3px;
}

.formInputs {
  height: 50vh;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  align-items: center;
}

form {
  display: flex;
  font-size: 1.5rem;
  flex-direction: column;
}

input {
  margin: 5px 0;
  width: 80%;
  font-size: 1.2rem;
  justify-content: center;
  border-radius: 5px;
  border: none;
  padding: 5px;
}

input:focus {
  outline: none;
}

button {
  background-color: lightseagreen;
  width: 3rem;
  margin: 15px 0;
  align-self: end;
  outline: none;
  border: none;
  border-radius: 5px;
  font-size: 1rem;
  height: 2rem;
  box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.5);
  cursor: pointer;
}

button:hover {
  background-color: gray;
}

.weatherResults {
  text-align: center;
  line-height: 1.3;
  font-size: 1.5rem;
}

.result {
  background-color: lightseagreen;
  padding: 16px;
  border-radius: 10px;
  animation: 1.5s ease-in fadeInAnimation;
  font-weight: bold;
}

.errorMsg {
  color: red;
  text-align: center;
  width: 30%;
}

@keyframes fadeInAnimation {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
     }
}
